import * as React from 'react';
import Stack from '@mui/material/Stack';
import Snackbar from '@mui/material/Snackbar';
import MuiAlert, { AlertProps } from '@mui/material/Alert';
import { useAppDispatch, useAppSelector } from "../../state/hooks/hooks-selectors";
import { setErrorAppAC, setSuccessAppAC } from "../../state/reducers/app/app-reducer";

const Alert = React.forwardRef<HTMLDivElement, AlertProps>(function Alert(
  props,
  ref,
) {
  return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});

export function SnackbarComponent() { //     success
  let error = useAppSelector<string | null>(state => state.app.error) //null -   
  let success = useAppSelector<string | null>(state => state.app.success) //null -   
  const dispatch = useAppDispatch()

  const handleClose = (event?: React.SyntheticEvent | Event, reason?: string) => {
    if (reason === 'clickaway') return
    dispatch(setErrorAppAC(null))
    dispatch(setSuccessAppAC(null))
  };

  if (!error && !success) {
    return null
  }

  return (
    <Stack sx={{ width: '100%' }}>
      <Snackbar open={!!error || !!success} autoHideDuration={3000} onClose={handleClose}>
        <Alert onClose={handleClose}
          severity={success ? "success" : "error"}
          sx={{ width: '100%' }}>{success ? success : error}
        </Alert>
      </Snackbar>
    </Stack >
  );
}